<template>
  <div class="root">
    <div class="header">
      <p class="header-title">{{msg}}</p>
      <el-select class="whose" v-model="whose" placeholder="请选择想要查看的策划" @change="changeData">
        <el-option
          v-for="item in userList"
          :key="item.id"
          :label="item.name"
          :value="item.id">
          <span style="float: left">{{ item.name }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.club }}</span>
        </el-option>
      </el-select>
    </div>
    <div class="content">
      <ul class="dataList">
        <li v-for="item in dataList" :key="item.title">
          <p class="post">post</p>
          <a class="work-title" :href="item.url" target="_blank" :title="item.title">{{item.title}}</a>
          <p class="psd">提取码【{{item.psd}}】</p>
        </li>
      </ul>
    </div>
    <div class="footer">
      <a href="https://weibo.com/u/3471637900" target="_blank">code by &copy; 墨白肀(yù)</a>
        <!-- &#9775; -->
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Home',
  data () {
    return {
      msg: '中抓策划分享 | Share Your Works',
      title: '这是作品标题',
      psd: 'sjkd',
      dataList: [],
      userList: [],
      whose: ''
    }
  },
  mounted () {
    this.queryWhose()
    this.defaultData()
  },
  methods: {
    queryWhose () {
      axios.get('http://115.159.191.153:3000/whose')
        .then(response => {
          this.userList = response.data.userList
          // console.log(this.userList)
        })
        .catch(error => {
          console.log(error)
          alert('网络错误，不能访问')
        })
    },
    defaultData () {
      console.log('22222')
      axios.get('http://115.159.191.153:3000/gjqf')
        .then(response => {
          this.dataList = response.data.works
          console.log(this.dataList)
        })
        .catch(error => {
          console.log(error)
          alert('网络错误，不能访问')
        })
    },
    changeData () {
      console.log('3333')
      axios.get('http://115.159.191.153:3000/' + this.whose)
        .then(response => {
          this.dataList = response.data.works
          console.log(this.dataList)
        })
        .catch(error => {
          console.log(error)
          alert('网络错误，不能访问')
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.header {
  width: 100vw;
  height: 8vw;
  background-image: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%);
}
.header-title {
  line-height: 8vw;
  font-size: 4vw;
  line-height: 5vw;
  color: white;
  text-align: center;
  font-family: 'Xiaowei';
}
.whose {
  /* display: inline-block; */
  font-size: 2vw;
  line-height: 3vw;
  position: relative;
  left: 5vw;
}
.content {
  width: 100vw;
  height: calc(100vh - 12vw);
  background-color: white;
  position: absolute;
  top: 8vw;
  left: 0;
}
.dataList {
  position: relative;
  width: 90vw;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* justify-content:content; */
  justify-content:space-between;
}
.dataList li {
  width: 20vw;
  height: 23vw;
  list-style: none;
  margin: 1rem;
  border-radius: 10px;
  background-image: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%);

}
.dataList li:hover {
  background-image: linear-gradient(to top, lightgrey 0%, lightgrey 1%, #e0e0e0 26%, #efefef 48%, #d9d9d9 75%, #bcbcbc 100%);

}
.post {
  width: 16vw;
  height: 16vw;
  background-color: #fff;
  margin: 2vw 2vw 1vw 2vw;
}
.work-title {
  display: block;
  text-decoration: none;
  width: 80%;
  margin: 0 auto;
  line-height: 2vw;
  font-size: 1.2vw;
  text-align: center;
  color: black;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.work-title:hover {
  text-decoration: underline;
}
.psd {
  width: 100%;
  line-height: 2vw;
  font-size: 1vw;
  text-align: center;
}
.footer {
  width: 100vw;
  height: 4vw;
  line-height: 4vw;
  font-size: 1.2vw;
  background-image: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%);
  color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: center;
}
.footer a {
  color: white;
  text-decoration: none;
}
.footer a:hover {
  text-decoration: underline;
}
.dataList::-webkit-scrollbar {
  width: 10px;
}
.dataList::-webkit-scrollbar-thumb {
  /* background: #FF7A9B; */
  background-image: linear-gradient(-20deg, #f794a4 0%, #fdd6bd 100%);
  border-radius: 10px;
}
.dataList::-webkit-scrollbar-track {
  background-color: transparent;
}
.el-input__inner {
  height: 2vw;
  line-height: 2vw;
  /* border: 1px solid #FF749B; */
}
.el-input__inner:hover,.el-input__inner:focus {
  border-color: #DCDFE6 !important;
}
</style>



